import initialState from './initial-state';
import { USER_LOGIN_REQUEST, USER_LOGIN_SUCCESS, USER_LOGIN_FAILURE, USER_LOGOUT } from '../_actions/user';

export default function userReducer(state = initialState.user, action) {
    switch (action.type) {
        case USER_LOGIN_REQUEST:
            return {
                loggingIn: true,
                user: action.user
            };
        case USER_LOGIN_SUCCESS:
            return {
                loggedIn: true,
                user: action.user
            };
        case USER_LOGIN_FAILURE:
            return {};
        case USER_LOGOUT:
            return {};
        default:
            return state
    }
}
