import React from "react";
import ReactDOM from "react-dom";
import { Router, Route, Switch, Redirect } from "react-router-dom";

import { Provider } from 'react-redux';
import { PersistGate } from 'redux-persist/integration/react'
import { store, persistor } from './_helpers/store';

import AuthLayout from "layouts/Auth.js";
import RtlLayout from "layouts/RTL.js";
import AdminLayout from "layouts/Admin.js";
import ClientDashboard from "views/ClientDashboard/EventsView"
import "assets/scss/material-dashboard-pro-react.scss?v=1.8.0";

import {isAuthenicated} from "./_helpers/is-authenticated";
import history from './_helpers/history';

ReactDOM.render(
    <Provider store={store}>
        <PersistGate loading={null} persistor={persistor}>
          <Router history={history}>
            <Switch>
              <Route path="/rtl" component={RtlLayout} />
              <Route path="/auth" component={AuthLayout} />
              <Route path="/admin" component={AdminLayout} />
              <Route path="/client" component={ClientDashboard} />
                {isAuthenicated() ? <Redirect from="/" to="/client" /> : <Redirect from="/" to="/auth" /> }
            </Switch>
          </Router>
        </PersistGate>
    </Provider>,
  document.getElementById("root")
);
