import {userApi } from "../_api/user-api";
import history from '../_helpers/history'


export const USER_LOGIN_REQUEST = 'USER_LOGIN_REQUEST';
export const USER_LOGIN_SUCCESS = 'USER_LOGIN_SUCCESS';
export const USER_LOGIN_FAILURE = 'USER_LOGIN_FAILURE';

export const USER_LOGOUT = 'USER_LOGOUT';

export function login(email, password) {
    console.log(" user action is called ");
    return dispatch => {
        dispatch(request({ email }));

        userApi.login(email, password)
            .then( result => {
                    console.log(result);
                    dispatch(success(email));
                    history.push("/admin");
                },
                (error) => {
                    console.log(error);
                    dispatch(failure(email));
                }

            )
    };

    function request(user) { return { type: USER_LOGIN_REQUEST, user } }
    function success(user) { return { type: USER_LOGIN_SUCCESS, user } }
    function failure(error) { return { type: USER_LOGIN_FAILURE, error }}
}

export function logout() {
    userApi.logout();
    return { type: USER_LOGOUT};
}
